import BurgerMenu from './LottieAnimations/burger-menu.json'
import Lottie from 'lottie-react-web'
import React from 'react';
import { HashLink } from 'react-router-hash-link';


export class HamburgerMenu extends React.Component {
    constructor(props) {
      super(props);
      this.state = {expanded: false};
      this.playerRef = React.createRef();
      this.toggle = this.toggle.bind(this);
      this.onScroll = this.onScroll.bind(this)
      window.addEventListener('scroll', this.onScroll)
    }
  
    toggle() {
      this.setState({expanded: !this.state.expanded});
    }
  
  
    onScroll() {
      this.setState({expanded: false})
    }
  
    render() {
      return (
        <div class={this.state.expanded ? "MenuContainer expanded":"MenuContainer"}>
          <button class="burgerMenuButton" onClick={this.toggle}>
            <Lottie 
                options={{
                    animationData: BurgerMenu,
                    loop: false,
                }}
                direction={this.state.expanded ? 1 : -1}
              />
          </button>
          <div class="LinkContainer" hidden={!this.state.expanded}>
            <HashLink to="./#start" class="page_references" hidden={!this.state.expanded} onClick={this.toggle}><b>Start</b></HashLink>
            <HashLink to="./projects#start" class="page_references" hidden={!this.state.expanded} onClick={this.toggle}><b>Projects</b></HashLink>
            <HashLink to="./#education" class="page_references" hidden={!this.state.expanded} onClick={this.toggle}><b>Education</b></HashLink>
            <HashLink to="./#skills" class="page_references" hidden={!this.state.expanded} onClick={this.toggle}><b>Skills</b></HashLink>
            <HashLink to="./#certifications" class="page_references" hidden={!this.state.expanded} onClick={this.toggle}><b>Certifications</b></HashLink>
            <HashLink to="./#languages" class="page_references" hidden={!this.state.expanded} onClick={this.toggle}><b>Languages</b></HashLink>
            <HashLink to="./#contact" class="page_references" hidden={!this.state.expanded} onClick={this.toggle}><b>Contact</b></HashLink>
          </div>
        </div>
      );
    }
}


